"use client";
import { useState, useEffect } from "react";
import dynamic from "next/dynamic";
import L from "leaflet";
import "leaflet/dist/leaflet.css";

// Fix leaflet marker icons
delete L.Icon.Default.prototype._getIconUrl;
L.Icon.Default.mergeOptions({
  iconRetinaUrl:
    "https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.7.1/images/marker-icon-2x.png",
  iconUrl:
    "https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.7.1/images/marker-icon.png",
  shadowUrl:
    "https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.7.1/images/marker-shadow.png",
});

// Dynamically import react-leaflet components (client only)
const MapContainer = dynamic(
  () => import("react-leaflet").then((mod) => mod.MapContainer),
  { ssr: false }
);
const TileLayer = dynamic(
  () => import("react-leaflet").then((mod) => mod.TileLayer),
  { ssr: false }
);
const Marker = dynamic(
  () => import("react-leaflet").then((mod) => mod.Marker),
  { ssr: false }
);
const Popup = dynamic(
  () => import("react-leaflet").then((mod) => mod.Popup),
  { ssr: false }
);
const useMapEvents = dynamic(
  () => import("react-leaflet").then((mod) => mod.useMapEvents),
  { ssr: false }
);

// Map click handler
function MapClickHandler({ onMapClick }) {
  const mapEvents = require("react-leaflet").useMapEvents;
  mapEvents({
    click(e) {
      onMapClick(e);
    },
  });
  return null;
}

export default function ReportPage() {
  const [description, setDescription] = useState("");
  const [position, setPosition] = useState(null);
  const [imageBase64, setImageBase64] = useState("");
  const [message, setMessage] = useState("");
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [userLocation, setUserLocation] = useState([24.7136, 46.6753]); // Default Riyadh

  // Get user location
  useEffect(() => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (pos) => {
          const newPos = [pos.coords.latitude, pos.coords.longitude];
          setUserLocation(newPos);
          setPosition(newPos);
        },
        (err) => {
          console.error("Error getting location: ", err);
        }
      );
    }
  }, []);

  // Handle image upload
  const handleImageChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      if (!file.type.startsWith("image/")) {
        setMessage("‚ùå Only images are allowed!");
        return;
      }
      if (file.size > 5 * 1024 * 1024) {
        setMessage("‚ùå Image size must be less than 5MB!");
        return;
      }
      const reader = new FileReader();
      reader.onload = (event) => {
        setImageBase64(event.target.result);
      };
      reader.readAsDataURL(file);
      setMessage("");
    }
  };

  // Handle map click
  const handleMapClick = (e) => {
    setPosition([e.latlng.lat, e.latlng.lng]);
  };

  // Submit report
  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!position) {
      setMessage("‚ùå Please select a location on the map");
      return;
    }

    setIsSubmitting(true);

    try {
      const token = localStorage.getItem("authToken");

      if (!token) {
        setMessage("‚ö†Ô∏è Please log in first");
        setIsSubmitting(false);
        return;
      }

      const res = await fetch("http://localhost:5000/reports/create", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify({
          description,
          location: { lat: position[0], lng: position[1] },
          image: imageBase64,
        }),
      });

      const data = await res.json();

      if (res.ok) {
        setMessage("‚úÖ Report submitted successfully!");
        setDescription("");
        setPosition(null);
        setImageBase64("");

        setTimeout(() => {
          window.location.href = "/";
        }, 2000);
      } else {
        setMessage(data.error || data.message || "‚ùå Failed to submit report");
      }
    } catch (error) {
      console.error(error);
      setMessage("üö® Server error, please try again later");
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="container py-5">
      <h1 className="mb-4 text-danger">Submit a New Report</h1>

      <form className="w-75 mx-auto" onSubmit={handleSubmit}>
        {/* Description */}
        <div className="mb-3">
          <label className="form-label">Description</label>
          <textarea
            className="form-control"
            placeholder="Write your report details here..."
            value={description}
            onChange={(e) => setDescription(e.target.value)}
            required
            rows="4"
          />
        </div>

        {/* Map */}
        <div className="mb-3">
          <div className="d-flex justify-content-between align-items-center mb-2">
            <label className="form-label mb-0">Select Location on Map</label>
            <button
              type="button"
              className="btn btn-outline-primary btn-sm"
              onClick={() => {
                if (navigator.geolocation) {
                  navigator.geolocation.getCurrentPosition(
                    (pos) => {
                      const newPos = [pos.coords.latitude, pos.coords.longitude];
                      setUserLocation(newPos);
                      setPosition(newPos);
                      setMessage("üìç Your current location was set");
                    },
                    (err) => {
                      setMessage("‚ùå Failed to get your current location");
                    }
                  );
                }
              }}
            >
              üìç Use My Current Location
            </button>
          </div>

          <div style={{ height: "400px", width: "100%" }}>
            <MapContainer
              center={userLocation}
              zoom={13}
              style={{ height: "100%", width: "100%" }}
            >
              <TileLayer
                attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a>'
                url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
              />
              <MapClickHandler onMapClick={handleMapClick} />
              {position && (
                <Marker position={position}>
                  <Popup>Selected Location</Popup>
                </Marker>
              )}
            </MapContainer>
          </div>

          <div className="form-text mt-2">
            Click on the map to select the location
            {position && (
              <span className="ms-2">
                Selected: {position[0].toFixed(6)}, {position[1].toFixed(6)}
              </span>
            )}
          </div>
        </div>

        {/* Image */}
        <div className="mb-3">
          <label className="form-label">Report Image</label>
          <input
            type="file"
            className="form-control"
            accept="image/*"
            onChange={handleImageChange}
          />
          <div className="form-text">Only images are allowed (Max size: 5MB)</div>
        </div>

        {imageBase64 && (
          <div className="mb-3">
            <label className="form-label">Image Preview:</label>
            <div>
              <img
                src={imageBase64}
                alt="Preview"
                className="img-thumbnail mt-2"
                style={{ maxWidth: "300px", maxHeight: "300px" }}
              />
            </div>
          </div>
        )}

        {/* Submit */}
        <button
          type="submit"
          className="btn btn-danger w-100"
          disabled={isSubmitting}
        >
          {isSubmitting ? "Submitting..." : "Submit Report"}
        </button>

        {message && <p className="mt-3 text-center">{message}</p>}
      </form>
    </div>
  );
}
